.loadContainer{
    display: flex;
    flex-direction: column;
    align-items: center;

}
.loadContainer span {
    margin: 15px;
    width: 500px;
    height: 50px;
    border: 1px solid black;
    border-radius: 5px;
    background: linear-gradient(90deg, rgba(226,226,242,1) 3%, rgba(219,219,219,1) 35%, rgba(243,243,243,0.13786764705882348) 100%);
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
    border: none;

     
}

@keyframes loadingAnimation {
    0%{
        background-position: 0% 50% ;
    }
    50%{
        background-position: 100% 50% ;
    }
    
    100%{
        background-position: 0% 50%;
    }
    
    
}
