.Icon-check--active{
    color: #59e200;
    font-weight: 600;
}
li{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    position: relative;
    margin-top: 10px;
    background-color: #f2f2f2;
    border-radius: 4px;
}

li p{
    width: 450px;
}

li input[type ="checkbox"]{
    width: 20px;
    height: 20px;
 
}

input[type ="checkbox"]:checked{
   appearance: none;
   width: 20px;
   height: 20px;
   border: none;
}


 input[type ="checkbox"]:checked:after{
    content:"\2705";
    display: inline-block;
    width: 20px;
    height: 20px;
    border: none;
    background: transparent;
    border-radius: 5px;
    
 
 }

 .task-complete-true{
    border-radius: 4px;
    /* background: linear-gradient(to right, #ffcc00, #ff6600); */
    background: linear-gradient(90deg, rgba(254,254,255,1) 3%, rgba(23,231,68,1) 35%, rgba(0,212,255,1) 100%);
    }

.text-complete-true{
    background: transparent;
    text-decoration: line-through;
}

