ul {
    width: 90%;
    list-style: none;
    height: 270px;
    overflow-y: scroll;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background-color: #f2f2f2;
}

::-webkit-scrollbar-thumb {
    height: 5px;
    width: 5px;
    background-color: #c9c4c4;
    border-radius: 10px;
}