form {
    height: 100%;
    display: flex;
    flex-direction: column;

}

form button{
    color: white;
    width: 90px;
    height: 30px;
    margin: 5px;
    border-radius: 0px;
    border: none;
    cursor: pointer;
    border-radius: 20px;
    background: #81bc5b;
}
form textarea{
    margin-top: 21px;
    width: 410px;
    height: 70px;
}

form .containerButtons{
    position: relative;
    top: 50px;
}


.parentTask{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #161669;
    align-items: center;
    justify-content: center;
    border-radius: 7px;
    box-shadow: 1px 1px 1px black;
}

.parentTask h1{
    font-size: 40px;
    color: aliceblue;
    padding: 0 48px 48px 48px;
    position: relative;
    bottom: 70px;
}

.parentTask .inputArea input {
    width: 170px;
    outline: none;
    font-size: 15px;
    padding: 0 30px;
    line-height: 40px;
    background: transparent;
    border: 1.2px solid white;
    border-radius: 10px;
    z-index: 111;
    position: absolute;
}

.inputArea{
    position: relative;
    bottom: 0;
    height: 10%;
    width: 198px;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    font-size: 22px;
    padding: 0 30px;
    line-height: 52px;
    border-radius: 10px;
}

.inputArea input{
    color: white;
}

.parentTask .inputArea label {
    position: absolute;
    font-size: 19px;
    color: white;
    padding: 0 12px;
    margin: 0 10px;
    transition: all 0.2s ease;
    left: 0;
    opacity: 0.5;
    height: auto;

}

.parentTask .inputArea input:focus{
    border: 2px solid #92fc4d;
}

.parentTask .inputArea input:focus + label  {
    transform: translate(-8px, -26px) scale(0.88);
    z-index: 1111;
    background: #161669;
    color: #59e200;
    opacity: 1;
    line-height: 1.1;

}

input:not(:placeholder-shown):not(:focus) ~ label {
    transform: translate(-8px, -35px) scale(0.88);
    color: #59e200;
    line-height: 1.1;
    opacity: 1;
    background: #161669;
    z-index: 1111;
}



.parentTask .inputArea input:not(:placeholder-shown):not(:focus) {
    border:2px solid #59e200;

}






