.CreateTodoButton{
    height: 32px;
    width: 117px;
    border-radius: 6px;
    border: none;
    font-size: 17px;
    cursor: pointer;
    transition: all 1s ease;
    background: #59e200;
    color: white;
    margin-top: 36px;
    box-shadow: 1px 1px 0px 0px black;
}
.CreateTodoButton:hover{
    transform: scale(1.1);

} 