.parent {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100vh;
}

.parent .child{
  width: 69%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}


.containerForm{
      width: 500px;
      height: 300px;
      background: white;
      position: absolute;
      left: calc(50% - 250px);
      top: calc(50% - 150px);
      border-radius: 5px;
}

.noData{
  width: 174px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  left: calc(50% - 100px);
  top: calc(69% - 100px);
}
.p{
  color: #939aa2;
  font-size: 19px;
  font-weight: 600;
}