
.modal{
    width: 300px;
    height: 200px;
    background: white;
    display: flex;
    flex-direction: column;
    position: absolute;
    left: calc(50% - 150px);
    top: calc(50% - 100px);
    border-radius: 7px;
}


  .containerModal{
    position: absolute;
    top: 0;
    background: #000000b0;
    width: 100vw;
    height: 100vh;
  }
